.background-color {
	background-image: -webkit-gradient(linear, right top, left top, from(#FEE140), to(#FA709A));
	background-image: -webkit-linear-gradient(right, #FEE140 0%, #FA709A 100%);
	background-image: -o-linear-gradient(right, #FEE140 0%, #FA709A 100%);
	background-image: linear-gradient(to left, #FEE140 0%, #FA709A 100%);
}



