.background-color_food {
	background-image: -webkit-gradient(linear, right top, left top, from(#4158D0), color-stop(46%, #C850C0), to(#FFCC70));
	background-image: -webkit-linear-gradient(right, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
	background-image: -o-linear-gradient(right, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
	background-image: linear-gradient(to left, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
}

.chart-basic-food {
	padding-top: 20px;
	padding-bottom: 20px;
	margin-top: 10px;
	margin-bottom: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	max-width:750px;
}


