.background {
	background-image: -webkit-gradient(linear, right top, left top, from(#21D4FD), to(#B721FF));
	background-image: -webkit-linear-gradient(right, #21D4FD 0%, #B721FF 100%);
	background-image: -o-linear-gradient(right, #21D4FD 0%, #B721FF 100%);
	background-image: linear-gradient(to left, #21D4FD 0%, #B721FF 100%);
}





